import React from 'react';
import { Global } from '@emotion/core';
import { Inspector } from 'react-dev-inspector';
import { Title } from '../components/Title';
import { Slogan } from '../components/Slogan';
import { KeyPad, Keypress } from '../components/Keypress';
import * as S from './styles';
const projectRepo = 'https://github.com/zthxxx/react-dev-inspector';
const isDev = process.env.NODE_ENV === 'development';
export const HomePage = () => {
  return <Inspector disableLaunchEditor={!isDev} onClickElement={inspect => {
    console.debug(inspect);
    if (isDev || !inspect.codeInfo) return;
    const {
      relativePath,
      lineNumber
    } = inspect.codeInfo;
    window.open(`${projectRepo}/blob/master/sites/umi3/${relativePath}#L${lineNumber}`);
  }} data-inspector-line="15" data-inspector-column="4" data-inspector-relative-path="src/layouts/index.tsx">
      <Global styles={S.globalCss} data-inspector-line="31" data-inspector-column="6" data-inspector-relative-path="src/layouts/index.tsx" />


      <S.Base data-inspector-line="34" data-inspector-column="6" data-inspector-relative-path="src/layouts/index.tsx">
        <S.GithubCorner href={projectRepo} data-inspector-line="35" data-inspector-column="8" data-inspector-relative-path="src/layouts/index.tsx" />

        <Title data-inspector-line="39" data-inspector-column="8" data-inspector-relative-path="src/layouts/index.tsx">
          <span data-inspector-line="40" data-inspector-column="10" data-inspector-relative-path="src/layouts/index.tsx">React Dev Inspector</span>
        </Title>

        <Slogan data-inspector-line="43" data-inspector-column="8" data-inspector-relative-path="src/layouts/index.tsx">
          <p data-inspector-line="44" data-inspector-column="10" data-inspector-relative-path="src/layouts/index.tsx">Quick jump to local IDE source code directly from browser React component by just a simple click!</p>
          <p data-inspector-line="45" data-inspector-column="10" data-inspector-relative-path="src/layouts/index.tsx"><small data-inspector-line="45" data-inspector-column="13" data-inspector-relative-path="src/layouts/index.tsx">( for this prod online demo page, jump to GitHub file )</small></p>
        </Slogan>

        <KeyPad data-inspector-line="48" data-inspector-column="8" data-inspector-relative-path="src/layouts/index.tsx">
          <Keypress data-inspector-line="49" data-inspector-column="10" data-inspector-relative-path="src/layouts/index.tsx">Ctrl ‚åÉ</Keypress>
          +
          <Keypress data-inspector-line="51" data-inspector-column="10" data-inspector-relative-path="src/layouts/index.tsx">Shift ‚áß</Keypress>
          +
          <Keypress data-inspector-line="53" data-inspector-column="10" data-inspector-relative-path="src/layouts/index.tsx">Command ‚åò</Keypress>
          +
          <Keypress data-inspector-line="55" data-inspector-column="10" data-inspector-relative-path="src/layouts/index.tsx">C</Keypress>
        </KeyPad>
      </S.Base>
    </Inspector>;
};
export default HomePage;